                title:
                  "Tokamak Network has signed MOU with Ozys and supports KlaySwap [EN/KR]",
                pubDate: "2021-06-01 07:00:08",
                link:
                  "https://medium.com/onther-tech/tokamak-network-has-signed-mou-with-ozys-and-supports-klayswap-en-kr-e280b4426810?source=rss----eb8117f0bafd---4",
                guid: "https://medium.com/p/e280b4426810",
                author: "Onther",
                thumbnail:
                  "https://cdn-images-1.medium.com/max/1024/1*RFNH3BMC2q7-Kb6fBz3cxA.png",
                description:
                  '\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1024/1*RFNH3BMC2q7-Kb6fBz3cxA.png"></figure><p>Hello!</p>\n<p>This is On-demand Ethereum Layer 2 Platform Tokamak Network.</p>\n<p>Tokamak Network has signed an MOU with Ozys, a blockchain technology company regarding the use of the Orbit bridge and research advice on the bridge between heterogeneous chains and Ethereum Layer 2. We will also participate in the KlaySwap developed byOzys.</p>\n<h3><strong>What isOzys?</strong></h3>\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1024/1*8_bH41RbWIHqeVh2eEec1Q.png"></figure><p>Ozys is operating <a href="https://klayswap.com/"><strong>Klayswap</strong></a>, a DeFi protocol launched last November based on Klaytn which is the blockchain platform of Kakaos blockchain technology affiliate Ground X. KlaySwap is a decentralized financial swap protocol based on Automated Market Maker (AMM). Currently, it has a total value of about $1.3 billion in its liquidity and deposits.</p>\n<h3>What we willdo?</h3>\n<p>The details of this business agreement are asfollows.</p>\n<ul>\n<li>TON ecosystem expansion using Orbit chain bridge (Klaytn, BSC, HECO,etc.)</li>\n<li>Research advice on the bridge between heterogeneous chains and Ethereum Layer2</li>\n<li>Providing liquidity for KLAY-(K)TON inKlayswap</li>\n<li>Joint marketing</li>\n</ul>\n<h3>What can weexpect?</h3>\n<p>We plan to expand the TON ecosystem through Orbit Bridge, a tool that supports token swap between mainnets of heterogeneous chains developed by Ozys. The TON ecosystem is planned to expand to chains where the DeFi ecosystem is rapidly growing, such as Klaytn, Binance Smart Chain (BSC), and Huobi ECO chain (HECO). We expect that global users who have had difficult access to our ecosystem will be able to more easily participate through Klayswap (Klaytn), Pancake Swap (BSC), andmore.</p>\n<p>Tokamak Network will also provide advice on research for a bridge between Ethereum-based Layer 2 and other mainnets provided Orbit Bridge supports Ethereum-based Layer2.</p>\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1024/1*yAzUZe-GFbUfQ6TrXDOuhw.png"></figure><p>In order to deposit TON into KlaySwap, users first need to convert the mainnet to KTON through <a href="https://bridge.orbitchain.io/"><strong>Orbit Bridge</strong></a>, a tool that supports token conversion between mainnets. (<a href="https://bridge.orbitchain.io/assets/orbitbridge_guide_en.pdf"><strong>Conversion Guide</strong></a>) Tokamak Network expects to secure liquidity between the two cryptocurrencies by creating a TON-Klaytn pair and supplying initial liquidity to Klayswap which operates as a 100% smart contract on the Klaytn blockchain. Through this, users can directly exchange TON and Klay without a separate intermediate process, and users who provide liquidity can also receive a share of the fee generated from thepool.</p>\n<h3>One MoreThing</h3>\n<p>Tokamak Network is holding an airdrop event within KlaySwap to commemorate the cooperation with Ozys and support for KlaySwap.</p>\n<p>The event will be held for 2 months from the creation of the TON-KLAY pool in KlaySwap, and airdrops will be distributed to participants according to their contribution. You can check the details related to the event on the <a href="https://klayswap.com/">KlaySwapwebpage</a>.</p>\n<p>We believe that this agreement has opened up the possibility of using TON in other chain ecosystems. In particular, it is expected that the ecosystem of Klaytn, one of the most attention-grabbing blockchain projects in Korea, can be connected with TokamakNetwork.</p>\n<p>Thank you!</p>\n<p></p>\n<p>   2   .</p>\n<p>            2      (MOU)    .</p>\n<h3>What isOzys?</h3>\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1024/1*8_bH41RbWIHqeVh2eEec1Q.png"></figure><p>     X        <a href="https://klayswap.com/"><strong></strong></a>  11   .   (Automated Market Maker, AMM)        13      .</p>\n<h3>What we willdo?</h3>\n<p>      .</p>\n<ul>\n<li>    TON  (Klaytn, BSC, HECO)</li>\n<li> 2    </li>\n<li> (Klayswap) KLAY-(K)TON </li>\n<li> </li>\n</ul>\n<h3>What can weexpect?</h3>\n<p>            , TON    . Klaytn, Binance Smart Chain(BSC), Huobi ECO chain(HECO)  DeFi      TON   . Klayswap(Klaytn), Pancake Swap(BSC) ,      TON        .</p>\n<p>    2  ,     2         .</p>\n<figure><img alt="" src="https://cdn-images-1.medium.com/max/1024/1*yAzUZe-GFbUfQ6TrXDOuhw.png"></figure><p>TON          <a href="https://bridge.orbitchain.io/"><strong></strong></a>  KTON     (<a href="https://bridge.orbitchain.io/assets/orbitbridge_guide_ko.pdf"><strong> </strong></a>).     100%     -     ,        .             ,          .</p>\n<h3>One morething</h3>\n<p>           .</p>\n<p>   TON-KLAY    2,        .     <a href="https://klayswap.com/"><strong> </strong></a> .</p>\n<p>             .                  .</p>\n<p>.</p>\n<h3>* Tokamak OfficialChannels</h3>\n<p>Tokamak Global Telegram: <a href="https://t.me/tokamak_network">https://t.me/tokamak_network</a><br>Tokamak Global Discord: <a href="https://discord.gg/WjwRezbJY8">https://discord.gg/WjwRezbJY8</a><br>Tokamak Network Homepage: <a href="https://tokamak.network/#/">https://tokamak.network/#/</a><br>Tokamak Network Twitter: <a href="https://twitter.com/tokamak_network">https://twitter.com/tokamak_network</a><br>Tokamak Network Medium: <a href="https://medium.com/onther-tech">https://medium.com/onther-tech</a></p>\n<img src="https://medium.com/_/stat?event=post.clientViewed&amp;referrerSource=full_rss&amp;postId=e280b4426810" width="1" height="1" alt=""><hr>\n<p><a href="https://medium.com/onther-tech/tokamak-network-has-signed-mou-with-ozys-and-supports-klayswap-en-kr-e280b4426810">Tokamak Network has signed MOU with Ozys and supports KlaySwap [EN/KR]</a> was originally published in <a href="https://medium.com/onther-tech">Onther</a> on Medium, where people are continuing the conversation by highlighting and responding to this story.</p>\n',
